@import url('https://fonts.googleapis.com/css?family=Inter');
@import url(//fonts.googleapis.com/css?family=Arizonia);

body {
  margin: 0;
  font-family: 'Inter', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}



/**************************************************************************/
/** Variables
/**************************************************************************/

:root {
  --header-height: 80px;
  --header-font-color: #303131;
}


/**************************************************************************/
/** Container
/**************************************************************************/

.mainDiv {
    width: 100%;
    height:100%;
    background-color: #fff;
}


/**************************************************************************/
/** Header
/**************************************************************************/

.header {
    height: var(--header-height);
    text-align: left;
    line-height: var(--header-height);
}



.header a,
.header a:hover,
.header a:focus {
  text-decoration: none;
  cursor: pointer;
  color:#000;
}

.header .logo {
  display: inline-block;
  font-family: 'Arizonia', cursive;
  font-size: 53px;
  line-height: 53px;
  color: #cdb56c;
  margin: 10px 10px 0;
}


/**************************************************************************/
/** Menu Button
/**************************************************************************/

.menu-button {
  width: 53px;
  height: 53px;
  position: absolute;
  right: 0;
  z-index: 1;
  opacity: 0;
}


.menu-button-container {
    height: 30px;
    width: 30px;
    cursor: pointer;
    position: absolute;
    right: 15px;
    top: 35px;
}


.menu-button-icon,
.menu-button-icon::before,
.menu-button-icon::after {
    display: block;
    background-color: var(--header-font-color);
    position: absolute;
    height: 3px;
    width: 30px;
    transition: transform 400ms cubic-bezier(0.23, 1, 0.32, 1);
    border-radius: 2px;
}

.menu-button-icon::before {
    content: '';
    margin-top: -10px;
}

.menu-button-icon::after {
    content: '';
    margin-top: 10px;
}



/**************************************************************************/
/** Tabs
/**************************************************************************/

.tabbar {
  display: inline-block;
  float: right;
  position: relative;
  overflow: hidden;
}

.tabbar input{
  cursor: pointer;
}

.tabs {
  position: absolute;
  top: 5px;
  left: 0;
}

.tabs div {
  position: relative;
  display: inline-block;

  height: 29px;
  line-height: 29px;
  padding: 0 16px;
  margin-left: -1px;
  z-index: 0;
}

.tabs div:hover,
.tabs div:focus{

}

.tabs div.active {

}


.tabs a,
.tabs a:hover,
.tabs a:focus {
  text-decoration: none;
  cursor: pointer;
  width: 25%; /* adjust to the number of tabs */
  text-align: right;
  color: #303131;
  transition: color 0.4s ease 0s;
}

.tabs a:hover,
.tabs a:focus{
  color: #3366CC;
}

.tabs a .active {
  color: #8B98A7;
}





.tabs div:after{
  content: '';
  position: relative;
  width: 0; height: 2px;
  display: block;
  margin-top: 0px;
  right: 0;
  background: #1c5a94;
  transition: width .15s ease;
  -webkit-transition: width .15s ease;
}



.tabs div:hover, .tabs div:active, .tabs div:focus{
  opacity: 1.0;
  transition: all .4s ease;
  -webkit-transition: all .4s ease;
  outline: 0 none;
}

.tabs div:hover:after{
  width: 100%;
  left: 0;
  background: #1c5a94;
  opacity: 0.6;
}


@media (min-width:1px)  {

  /*
   * Hide tabs and show menu button
   */
    .tabs {
        display: none;
    }

    .menu-button, .menu-button-container {
        display: block;
    }



  /*
   * Update tab style for mobile
   */
    .tabbar {
        overflow: initial;
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
    }


    .tabs {
        position: absolute;
        background: #ededed;

        /* Hide the menu off-screen to the left */
        right: -100%;
        -webkit-transition: right 0.3s ease;
        -o-transition: left 0.3s ease;
        transition: right 0.3s ease;
        top: var(--header-height);
        display: inline-block;
        width: 100%;
        height: 100%;
        overflow-x: hidden;
        width: 0px;
    }

    .tabs div {
        padding: 0 30px;
        font-size: 22px;
        width: 100%;
        background: #000;
        line-height: 50px;
        height: 50px;
        border: 1px solid #777;
        color: #fff;
        text-align: left;
        display: block;
    }

    .tabs div a,
    .tabs div a:hover,
    .tabs div a:focus {
      color: #fff;
    }

    .tabs div.active {
        background: #757575;
    }

    .tabs div.active:after,
    .tabs div:after{
        height: 0px;
    }

    .tabs div:hover{
        background: #4f4f4f;
    }

    /*
     * Slide the menu in from left when the input is checked
     */
    .tabbar input:checked ~ .tabs {
        right: 0;
        width: 100%;
        height: calc(100% - var(--header-height));
        background-color: #4a4a4a;
        z-index: 2;
    }

}


@media (min-width:850px)  { /* Adjust width to fit tabs */

  /*
   * Show tabs and hide menu button
   */
    .tabs {
        display: block;
    }

    .menu-button, .menu-button-container {
        display: none;
    }


  /*
   * Reset everything that was changed above, when min-size = 1
   */
    .tabbar {
        float: right;
        position: relative;
        overflow: initial;
        width: initial;
        height: initial;
    }


    .tabs {
        display: initial;
        width: initial;
        overflow: initial;
        position: initial;
        height: initial;
        background: initial;
    }


    .tabs div {
        display: inline-block;
        padding: 35px 35px 0;
        font-size: 16px;

        background: none;

        width: initial;

        line-height: initial;
        height: initial;
        border: none;
        color: initial;
        text-align: initial;

    }

    .tabs div a,
    .tabs div a:hover,
    .tabs div a:focus {
      color: #303131;
    }

    .tabs div.active {
        background: none;
    }

    .tabs div.active:after,
    .tabs div:after{
        height: 2px;
    }

    .tabs div:hover{
        background: none;
    }

    .tabbar input:checked ~ .tabs {
        background-color: initial;
    }

}


/**************************************************************************/
/** Content
/**************************************************************************/

.content {
  padding: 10px;
}


/**************************************************************************/
/** Breadcrumbs
/**************************************************************************/

.breadcrumbs > div {
  display:inline-block;
  font-size: 13px;
  padding: 0 8px 0 0;
}


.breadcrumbs {
  border: 1px solid #cbd2d9;
  border-radius: 0.3rem;
  display: inline-flex;
  overflow: hidden;
}

.breadcrumbs a {
  background: #fff;
  color: #333;
  outline: none;
  padding: 0.75em 0.75em 0.75em 1.25em;
  position: relative;
  text-decoration: none;
  transition: background 0.2s linear;
}

.breadcrumbs a:hover:after,
.breadcrumbs a:hover {
  background: #edf1f5;
}

.breadcrumbs a:focus:after,
.breadcrumbs a:focus,
.breadcrumbs a.is-active:focus {
  background: #323f4a;
  color: #fff;
}

.breadcrumbs a:after,
.breadcrumbs a:before {
  background: white;
  bottom: 0;
  clip-path: polygon(50% 50%, -50% -50%, 0 100%);
  content: "";
  left: 100%;
  position: absolute;
  top: 0;
  transition: background 0.2s linear;
  width: 1em;
  z-index: 1;
}

.breadcrumbs a:before {
  background: #cbd2d9;
  margin-left: 1px;
}

.breadcrumbs a:last-child {
  border-right: none;
}

.breadcrumbs a.is-active {
  background: #edf1f5;
}





/**************************************************************************/
/** Thumbnails
/**************************************************************************/

.thumbnails {
  text-align: center;
}

.thumbnail {
  display: inline-block;
  width: 300px;
  height: 300px;
  border: 1px solid #dcdcdc;
  position: relative;
  border-radius: 8px;
  margin: 20px;
  transition: all .4s ease;
  /*
  box-shadow: 0 12px 14px 0 rgba(0, 0, 0, 0.2), 0 13px 20px 0 rgba(0, 0, 0, 0.2);
  */
}

.thumbnail:hover{
  box-shadow: 0px 20px 14px 0 rgba(0, 0, 0, 0.2), 14px 20px 20px 0 rgba(0, 0, 0, 0.2);

}

.thumbnail div {
  text-align: center;
  background-repeat: no-repeat;
  background-size: cover;
  width: calc(100% - 10px);
  height: calc(100% - 10px);
  background-position-x: 10px;
  background-position-y: 25px;
}

.thumbnail a {
  color:inherit;
  text-decoration: none;
}